import { getActiveAccount, saveActiveAccount } from "./storage";
import MUTATION_TYPES from "./mutationTypes";
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        activeAccount: getActiveAccount(),
        pin: "",
        returnPath: "",
    },
    mutations: {
        [MUTATION_TYPES.ACTIVE_ACCOUNT](state, address) {
            saveActiveAccount(address);
            state.activeAccount = getActiveAccount();
        },
        [MUTATION_TYPES.SET_PIN](state, pin) {
            state.pin = pin;
        },
        [MUTATION_TYPES.CLEAR_PIN](state) {
            state.pin = "";
        },
        [MUTATION_TYPES.SET_RETURN_PATH](state, returnPath) {
            state.returnPath = returnPath;
        },
        [MUTATION_TYPES.CLEAR_RETURN_PATH](state) {
            state.returnPath = "";
        }
    },
});



