<template>
  <v-layout column>
    <img width="100%" src="../../../static/img/heroimage_send_large.png" />

    <v-container fluid>
      <v-layout column align-center>
        <p style="font-size: large">{{ $t('send.destination') }}</p>
        <v-card width="100%">
          <v-layout rows align-baseline>
            <v-flex xs9 offset-xs1>
              <v-text-field
                id="receiver-address"
                name="receiver-address-field"
                :label="$t('send.input_address')"
              ></v-text-field>
            </v-flex>
            <v-flex xs2>
              <v-btn icon>
                <img height="100%" src="../../../static/img/icons/icon_addressbook.png">
              </v-btn>
            </v-flex>
          </v-layout>
        </v-card>
        <v-flex align-center justify-center>
          <v-btn color="primary">OK</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </v-layout>
</template>

<script>
  export default {
    name: "SendView",
  }
</script>

