import Vue from 'vue';
import Router from 'vue-router';
import Top from '../components/top/Top.vue';
import Balance from '../components/detail/Balance.vue';
import WalletSelect from '../components/wallet/WalletSelect.vue';
import WalletCreationType from '../components/wallet/WalletCreationType.vue';
import WalletCreationName from '../components/wallet/WalletCreationName.vue';
import WalletCreationNew from '../components/wallet/WalletCreationNew.vue';
import WalletCreationEnd from '../components/wallet/WalletCreationEnd.vue';
import LessonIntroduction from '../components/lesson/LessonIntroduction.vue';
import LessonLevel from '../components/lesson/LessonLevel.vue';
import LessonBeginner from '../components/lesson/LessonBeginner.vue';
import LessonPrivateKeyCaution from '../components/lesson/LessonPrivateKeyCaution.vue';
import LessonPrivateKey from '../components/lesson/LessonPrivateKey.vue';
import LessonBeginnerBackupEnd from '../components/lesson/LessonBeginnerBackupEnd.vue';
import LessonBeginnerEnd from '../components/lesson/LessonBeginnerEnd.vue';
import LessonLogin from '../components/lesson/LessonLogin.vue';
import LessonLoginEnd from '../components/lesson/LessonLoginEnd.vue';
import LessonUser from '../components/lesson/LessonUser.vue';
import PinInput from '../components/PinInput.vue';
Vue.use(Router);
export default new Router({
    routes: [
        { path: '/', redirect: '/top' },
        { path: '/top', name: 'Top', component: Top },
        { path: '/balance', name: 'Balance', component: Balance },
        { path: '/wallet/select', name: 'WalletSelect', component: WalletSelect },
        { path: '/wallet/creation/type', name: 'WalletCreationType', component: WalletCreationType },
        { path: '/wallet/creation/name', name: 'WalletCreationName', component: WalletCreationName },
        { path: '/wallet/creation/new', name: 'WalletCreationNew', component: WalletCreationNew, props: (route) => ({ walletName: route.query.name }) },
        { path: '/wallet/creation/end', name: 'WalletCreationEnd', component: WalletCreationEnd },
        { path: '/lesson/introduction', name: 'LessonIntroduction', component: LessonIntroduction },
        { path: '/lesson/level', name: 'LessonLevel', component: LessonLevel },
        { path: '/lesson/beginner', name: 'LessonBeginner', component: LessonBeginner },
        { path: '/lesson/beginner_backup_end', name: 'LessonBeginnerBackupEnd', component: LessonBeginnerBackupEnd },
        { path: '/lesson/beginner_end', name: 'LessonBeginnerEnd', component: LessonBeginnerEnd },
        { path: '/lesson/key/caution', name: 'LessonPrivateKeyCaution', component: LessonPrivateKeyCaution },
        { path: '/lesson/key', name: 'LessonPrivateKey', component: LessonPrivateKey },
        { path: '/lesson/login', name: 'LessonLogin', component: LessonLogin },
        { path: '/lesson/login_end', name: 'LessonLoginEnd', component: LessonLoginEnd },
        { path: '/lesson/user', name: 'LessonUser', component: LessonUser },
        { path: '/pin/check', name: 'PinInputCheck', component: PinInput, props: { mode: 'check' } },
        { path: '/pin/confirm', name: 'PinInputConfirm', component: PinInput, props: { mode: 'confirm' } },
        { path: '/pin/register', name: 'PinInputRegister', component: PinInput, props: { mode: 'register' } },
    ]
});



