import { getDivided } from "./numberUtils";
export default class {
    constructor(namespaceId, name, quantity, divisibility) {
        this.namespaceId = namespaceId;
        this.name = name;
        this.quantity = quantity;
        this.amount = getDivided(quantity, divisibility);
        this.fullName = namespaceId + ':' + name;
    }
}



